export default async (episode) => {
  if (process.env.NODE_ENV === 'production' && 'Notification' in window && navigator.serviceWorker) {
    if (Notification.permission === 'default') await Notification.requestPermission()

    if (Notification.permission === 'granted' && typeof episode !== 'undefined') {
      navigator.serviceWorker.getRegistration().then(function (reg) {
        var options = {
          body: episode.title,
          icon: '/cover.png',
          vibrate: [100, 50, 100]
        }
        reg.showNotification('New Episode', options)
      })
    }
  }
}
